<template>
  <div>
    <da-manage-page>
      <el-form slot="search" :inline="true" :model="searchForm.formdata">
        <el-form-item label="登录账号">
          <el-input v-model="searchForm.formdata.account"></el-input>
        </el-form-item>

        <el-form-item label="用户姓名">
          <el-input v-model="searchForm.formdata.userName"></el-input>
        </el-form-item>

        <el-form-item label="登录时间">
          <el-input v-model="searchForm.formdata.time"></el-input>
        </el-form-item>

        <el-form-item label="IP地址">
          <el-input v-model="searchForm.formdata.ipAddress"></el-input>
        </el-form-item>

        <el-form-item>
          <el-button type="primary" @click="getTbList">查询</el-button>
        </el-form-item>
      </el-form>
      <da-table ref="table" slot="content" :colums="colsData" :table-list="tableList" :search-data="searchForm" @callBack="getTbList" :showSelection="false">

        <!-- <el-table-column property="operation" label="操作" width="150" slot="operation" align="center" fixed="right">
                    <template slot-scope="scope">
                        <el-button size="small" @click="editTb(scope.row)">编辑</el-button>
                        <el-button size="small" @click="delTb(scope.row)">删除</el-button>
                    </template>
                </el-table-column> -->

      </da-table>
    </da-manage-page>

    <da-dialog :title="title" :visible.sync="dialogVisible" @close="closeDialog('addForm')">
      <el-form label-width="90px" :model="addForm" :rules="rules" ref="addForm">

      </el-form>

      <div slot="footer" class="dialog-footer">
        <el-button type="primary" @click="saveDialog('addForm')">确 定</el-button>
        <el-button @click="dialogVisible = false">取 消</el-button>
      </div>
    </da-dialog>
  </div>
</template>
<script>
export default {
  name: "logLogin",
  data() {
    return {
      // 查询表单
      searchForm: {
        formdata: { account: "", userName: "", time: "", ipAddress: "" },
        currentPage: 1,
        pageSize: 20
      },
      tableList: {}, // 数据列表
      colsData: [
        // 表头
        { label: "登录账号", prop: "account", align: "left", sortable: true },
        { label: "用户姓名", prop: "userName", align: "left", sortable: true },
        {
          label: "登录时间",
          prop: "time",
          align: "left",
          format: "YYYY-MM-DD HH:mm:ss",
          sortable: true
        },
        { label: "IP地址", prop: "ipAddress", align: "left", sortable: true },
        { label: "浏览器信息", prop: "browser", align: "left", sortable: true },
        { label: "IP归属地", prop: "place", align: "left", sortable: true }
      ],
      dialogVisible: false,
      addForm: {},
      rules: this.eachValidate({})
    };
  },
  computed: {
    title() {
      //弹窗标题
      return this.addForm.id ? "编辑系统日志管理" : "新增系统日志管理";
    },
    urlType() {
      //URL请求方式
      return this.addForm.id ? "put" : "post";
    }
  },
  watch: {},
  methods: {
    getTbList() {
      let formName = "table";
      this.$ajax
        .get("rest/logLogin/page", {
          params: this.searchForm,
          loadingTarget: this.$refs[formName]
        })
        .then(res => {
          if (res.data.status) {
            this.tableList = res.data;
          }
        });
    },
    // editTb(row) {
    //     this.dialogVisible = true;
    //     this.addForm = JSON.parse(JSON.stringify(row));
    // },
    closeDialog(formName) {
      this.$nextTick(function() {
        this.$refs[formName].resetFields();
        this.addForm = {};
      });
    },
    saveDialog(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.$ajax[this.urlType]("rest/logLogin", this.addForm).then(res => {
            if (res.data.status) {
              this.$message.success(res.data.answerMsg);
              this.dialogVisible = false;
              this.getTbList();
            } else {
              this.$message.error(res.data.answerMsg);
            }
          });
        }
      });
    }
    // ,
    // delTb(row) {
    //   this.$confirm("是否删除该记录?", "提示", {
    //     confirmButtonText: "确定",
    //     cancelButtonText: "取消",
    //     type: "warning"
    //   }).then(() => {
    //     this.$ajax
    //       .delete("rest/logLogin", { params: { id: row.id } })
    //       .then(res => {
    //         if (res.data.status) {
    //           this.$message.success(res.data.answerMsg);
    //           this.getTbList();
    //         } else {
    //           this.$message.error(res.data.answerMsg);
    //         }
    //       });
    //   });
    // }
  },
  mounted() {
    this.getTbList();
  }
};
</script>



